.video_footer{
    position: relative;
    bottom: 20%;
    color: white;
    margin-left: 30px;
    text-shadow: 0px 0px 10px #000000;
}

.video_footer_text h3{
    padding-bottom: 20px;
    opacity: 90%;
}

.video_footer_text p{
    padding-bottom: 20px;
    opacity: 90%;
}

.video_footer_music {
    display: flex;
}

.video_footer_music_title{
    width: 80%;
    overflow-x: hidden;
}

.video_footer_music_title p {
    animation: moveText 5s infinite linear;
    padding-bottom: 0;
}

.video_footer_record {
    animation: spinTheRecord 2s infinite;
    position: absolute;
    bottom: -12px;
    /*bottom: px */
    right: 20px;
    height: 50px;
    filter: invert();
}

@keyframes spinTheRecord {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

@keyframes moveText {
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(-100%);
    }
}

@media only screen and (max-width: 440px){
    .video_footer{
      bottom: 22%;
    }

    .video_footer_music_title{
        width: 78%;
    }
  }